<template lang="pug">
section.blogs
    .container
        .title
            h2
                span {{ $t("blogs") }}
        .blogs-content
            .row
                div(class="col-md-6 col-lg-4 box" v-for="blog in BlogsData.blogs", :key="blog.id")
                    .blogs-box.wow.fadeInUp(data-wow-duration="1s")
                        .blogs-image
                            img(:src="blog.image", alt="")
                        .blogs-text
                            h3 {{ blog.title }}
                            div(v-html="blog.body")
                            .blogs-buttons
                                router-link(:to="`/blogs/${blog.url}`" class='btn hvr-sweep-to-bottom') {{ $t("see-more") }}
</template>

<script>
import { mapState } from "vuex";

export default {
    name: "Blogs",
    metaInfo() {
        return {
            title: process.env.VUE_APP_TITLE,
            titleTemplate: `%s | ${this.BlogsData.website_title}`,
            meta: [
                { charset: "utf-8" },
                {
                    name: "description",
                    content: this.BlogsData.meta_description,
                },
                {
                    name: "keywords",
                    content: this.BlogsData.meta_keywords,
                },
            ],
        };
    },
    mounted() {
        this.$store.dispatch("getBlogsData");
    },
    computed: {
        ...mapState(["BlogsData"]),
    },
};
</script>
